@import "//cdn.theme.iastate.edu/nimbus-sans/css/nimbus-sans.css";

@import "//cdn.theme.iastate.edu/merriweather/css/merriweather.css";

.grid {
  display: grid;
  grid-template-columns: 230px 1fr;
  grid-template-rows: auto 1fr auto;
}

@media (max-width: 768px) {
  .grid {
    grid-template-columns: 1fr;
  }
}

.grid .footer {
  grid-column: 1/-1;
}

.grid .side-nav {
  grid-column: 1/2;
}

@media (max-width: 768px) {
  .grid .side-nav {
    grid-column: 1/-1;
  }
}

.grid .body {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 1rem;
  width: 100%;
  max-width: 93.75rem;
}

@media (max-width: 768px) {
  .grid .body {
    grid-column: 1/-1;
  }
}

.grid .body main {
  grid-column: 1/10;
}

@media (max-width: 768px) {
  .grid .body main {
    grid-column: 1/-1;
  }
}

.admin-page .grid .body main {
  grid-column: 1/-1;
}

.grid .body .layout-sidebar-first {
  grid-column: 10/13;
}

.full {
  grid-column: 1/-1;
}

